import React from 'react';
import { Link } from 'react-router-dom'; // Import Link from react-router-dom
import CartIMG from '../../../assets/img/Cart.png';
import './Cart.css'; // Assuming you have a separate CSS file for styling

import { useSelector } from 'react-redux';  
const Cart = () => {
    const itemValue = useSelector(state => state.CartValue.value); // Select itemValue from Redux state
    const cartEmpty = itemValue === 0;  

    return (
        <Link to="/cart" className="cart-link"> {/* Wrap cart content with Link */}
            <div className="cart">
                <div className="cart-image-container">
                    <img src={CartIMG} alt="Cart" className="cart-image" />
                    {!cartEmpty && <span className="badge">{itemValue}</span>}
                    {cartEmpty && <span className="empty-badge">0</span>}
                </div>
                <div className="cart-info">
                    <p className="cart-text">Your Cart</p>
                </div>
            </div>
        </Link>
    );
}

export default Cart;
