import React,{useState} from 'react';
import './Login.css';
// import BGV1 from '../../../assets/video/background.mp4';
import BGV2 from '../../../assets/video/background2.mp4';

import {AddToDB} from '../../Redux/AllFormData.js';
import {useSelector,useDispatch} from 'react-redux';
function Login() {
	
	 
	
	const Data = useSelector(state=>state.Data.data);
	const dispatch = useDispatch(); 
	const [fromData,setfromData] = useState({
		username:"",
		email:"",
		phone:"",
		password:"",
	});
	
	function handalAlldata(e){
		const {id,value} = e.target;
		setfromData({
			...fromData,
			[id] :value
		});
		 
	};
	
	 
	function Submit(e){
		
		e.preventDefault();
		
	const Dupicate = Data.find(user => 
	user.username === fromData.username ||
	user.email === fromData.email || 
	user.phone === fromData.phone);
	if(!Dupicate){
		try {
            dispatch(AddToDB(fromData));
			console.log("data Pused to main db ",Data,fromData);
			 setfromData({
                username: "",
                email: "",
                phone: "",
                password: "",
            });
		 
        } catch (error) {
            console.error('Error dispatching action:', error);
        }
	}else{
		alert("Dupicate Data");
	}
		 
		
	}
	
    return (
        <div className="form-container">
            <video autoPlay muted loop className="background-video">
                <source src={BGV2} type="video/mp4" />
            </video>
            <div className="form-content">
                <form className="login-form" onSubmit={Submit}>
				
                    <div className="input-wrapper">
                        <input 
                            type="text" 
                            id="username" 
                            className="floating-input" 
                            placeholder=" " 
							maxLength="25"
							value={fromData.username}
							onChange={handalAlldata}
                            required 
                        />
                        <label htmlFor="username">Username</label>
                    </div>
					
                    <div className="input-wrapper">
                        <input 
                            type="email" 
                            id="email" 
                            className="floating-input" 
                            placeholder=" " 
						
							value={fromData.email}
							onChange={handalAlldata}
							
                            required 
                            pattern="[a-zA-Z0-9._%+-]+@gmail\.com" 
                            title="Email must end with @gmail.com"
                        />
                        <label htmlFor="email">Email</label>
                    </div>
					
                    <div className="input-wrapper">
                        <input 
                            type="tel" 
                            id="phone" 
                            className="floating-input" 
                            placeholder=" " 
							maxLength="10"
							value={fromData.phone}
							onChange={handalAlldata}
                            required 
                            pattern="[789][0-9]{9}" 
                            title="Phone number must start with 7, 8, or 9 and be 10 digits long"
                        />
                        <label htmlFor="phone">Phone No.</label>
                    </div>
					
                    <div className="input-wrapper">
                        <input 
                            type="password" 
                            id="password" 
                            className="floating-input" 
                            placeholder=" " 
							maxLength="8"
							value={fromData.password}
							onChange={handalAlldata}
                            required 
                        />
                        <label htmlFor="password">Password</label>
                    </div>
					
                    <button type="submit" 
					className="submit-btn">Submit</button>
					
                </form>
            </div>
        </div>
    );
}

export default Login;
